import axios from 'axios';

export default {
  get: function(filters){
    
    // TODO: match API requirements
    // For now, lets just serialize filters as query params in the format, ?filterId=selectecedValue1,selectedValue2,...
    let params = {};
    filters.forEach(filter => { 
      switch (filter.type){
        case 'multi_select':
          params[filter.id] = filter.selectedValues.join(',');
          break; 
        case 'numeric_range':
          params[filter.id] = filter.selectedValues.min + ',' + filter.selectedValues.max;  
          break;
        case 'quarter_range':
          params[filter.id] = [filter.selectedValues.minYear, filter.selectedValues.minQuarter, filter.selectedValues.maxYear, filter.selectedValues.maxQuarter].join(',')
          break;
        case 'polygon':
          params[filter.id] = filter.selectedValues.map(val => '(' + val.lat + ',' + val.lng + ')').join(',');
          break;
        default: 
          console.error('param serialization not configured for ', filter.type);
          break;
      }
    });

    return axios.get('https://sheet2api.firebaseio.com/test/djmi2s9b.json', {
      params: params
    });
  }
};


