        <div class="mdc-layout-grid__cell">
          <h1 class="mdc-typography--display1">Login to your account</h1>
          <div class="mdc-textfield mdc-textfield--fullwidth" id="usernameField" data-mdc-auto-init="MDCTextfield">
            <input type="text" class="mdc-textfield__input" id="username">
            <label for="username" class="mdc-textfield__label">Username</label>
          </div>
          <div class="mdc-textfield mdc-textfield--fullwidth" id="passwordField" data-mdc-auto-init="MDCTextfield">
            <input type="text" class="mdc-textfield__input" id="password">
            <label for="password" class="mdc-textfield__label">Password</label>
          </div>
          <p>
            <router-link class="mdc-button mdc-button--raised" to="/search">
              Log in
            </router-link>
          </p>
        </div>
        <div class="mdc-layout-grid__cell"></div>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: "Login"
};
</script>

<style scoped>

</style>



