import Vuex from 'vuex';
import loadGoogleMapsAPI from "load-google-maps-api";
import NumericRangeFilterValidator from './validators/NumericRangeFilterValidator';
import QuarterRangeFilterValidator from './validators/QuarterRangeFilterValidator';
import Properties from "./services/Properties";

const initStore = function () {
  const store = new Vuex.Store({
    state: {
      credits: 400,
      searchResults: [],
      searchResultsLoading: false,
      viewedProperty: null,
      propertiesForExport: [],
      map: {
        googleMaps: null,
        drawingManager: null,
        instance: null,
        markers: [],
        polygonSelection: null
      },
      filters: [
        {
          subject: 'location',
          name: "Polygon",
          id: "polygon",
          type: "polygon",
          selectedValues: [],
          errors: []
        },
        {
          subject: 'property',
          name: "Property Types",
          id: "property_types",
          type: "multi_select",
          options: [
            { name: "Office", value: "office" },
            { name: "Industrial", value: "industrial" },
            { name: "Retail", value: "retail" }
          ],
          selectedValues: [],
          errors: []
        },
        {
          subject: 'property',
          name: "Space Types",
          id: "space_types",
          type: "multi_select",
          options: [
            { name: "Office", value: "office" },
            { name: "Industrial", value: "industrial" },
            { name: "Retail", value: "retail" },
            { name: "Restaurant", value: "restaurant" }
          ],
          selectedValues: [],
          errors: []
        },
        {
          subject: 'property',
          name: "Tenant Net Rentable Area",
          id: "tenant_net_rentable_area",
          type: "numeric_range",
          unit: 'sf',
          min: 0,
          max: 50000000,
          selectedValues: {
            min: 0,
            max: 50000000,
          },
          errors: []
        },
        {
          subject: 'property', name: "Year Built",
          id: "year_built",
          type: "numeric_range",
          unit: '',
          min: 1500,
          max: (new Date()).getFullYear(),
          selectedValues: {
            min: 1500,
            max: (new Date()).getFullYear(),
          },
          errors: []
        },
        {
          subject: 'property', name: "% Office Finish",
          id: "percent_office_finish",
          type: "numeric_range",
          unit: '%',
          min: 0,
          max: 100,
          selectedValues: {
            min: 0,
            max: 100,
          },
          errors: []
        },
        {
          subject: 'property',
          name: "Clear Height",
          id: "clear_height",
          type: "numeric_range",
          unit: 'feet',
          min: 8,
          max: 100,
          selectedValues: {
            min: 8,
            max: 100,
          },
          errors: []
        },
        {
          subject: 'transaction',
          name: "Lease Execution Date",
          id: "lease_execution_date",
          type: "quarter_range",
          minYear: 1950,
          maxYear: (new Date()).getFullYear(),
          selectedValues: {
            minYear: 1950,
            minQuarter: 1,
            maxYear: (new Date()).getFullYear(),
            maxQuarter: 4
          },
          errors: []
        },
        {
          subject: 'transaction',
          name: "Lease Start Date",
          id: "lease_start_date",
          type: "quarter_range",
          minYear: 1950,
          maxYear: (new Date()).getFullYear(),
          selectedValues: {
            minYear: 1950,
            minQuarter: 1,
            maxYear: (new Date()).getFullYear(),
            maxQuarter: 4
          },
          errors: []
        },
        {
          subject: 'transaction',
          name: "Least Type",
          id: "lease_type",
          type: "multi_select",
          options: [
            { name: "Gross", value: "gross" },
            { name: "Modified Gross", value: "modified_gross" },
            { name: "Net", value: "net" },
            { name: "Absolute Net", value: "absolute_net" }
          ],
          selectedValues: [],
          errors: []
        },
        {
          subject: 'transaction',
          name: "Term - Months",
          id: "term_months",
          type: "numeric_range",
          unit: 'mo',
          min: 0,
          max: 1500,
          selectedValues: {
            min: 0,
            max: 1500,
          },
          errors: []
        },
        {
          subject: 'transaction',
          name: "Initial Rent/SF",
          id: "initial_rent_sf",
          type: "numeric_range",
          unit: 'sf',
          min: 0,
          max: 60,
          step: 5,
          selectedValues: {
            min: 0,
            max: 60,
          },
          errors: []
        },
        {
          subject: 'transaction',
          name: "Months of Free Rent",
          id: "months_of_free_rent",
          type: "numeric_range",
          unit: 'mo',
          min: 0,
          max: 1500,
          selectedValues: {
            min: 0,
            max: 1500,
          },
          errors: []
        },
        {
          subject: 'transaction',
          name: "Transaction Type",
          id: "transaction_type",
          type: "multi_select",
          options: [
            { name: "New Lease", value: "new_lease" },
            { name: "Renewal", value: "renewal" },
            { name: "Expansion", value: "expansion" },
            { name: "Contraction", value: "contraction" },
            { name: "Renewal/Expansion", value: "renewal_expansion" },
            { name: "Renewal/Contraction", value: "renewal_contraction" },
            { name: "Early Renewal", value: "early_renewal" },
            { name: "Assignment", value: "assignment" },
            { name: "Pre-lease", value: "pre_lease" },
            { name: "Sublease", value: "sublease" }
          ],
          selectedValues: [],
          errors: []
        },
      ],
    },

    actions: {
      togglePropertySelectedForExport({ commit }, propertyId) {
        commit("togglePropertySelectedForExport", propertyId);
      },

      viewPropertyDetailClicked({ commit, state }, propertyId) {
        // Deactivate the previous marker
        if (state.viewedProperty){
          const prevMarker = state.map.markers.find(marker => marker.propertyId === state.viewedProperty.id)
          if (prevMarker) prevMarker.marker.setIcon('/static/marker-gray.svg');    
        }
        
        // Commit the viewproperty change
        commit("viewProperty", propertyId);
        
        // Activate the new marker
        const newMarker = state.map.markers.find(marker => marker.propertyId === propertyId)
        if (newMarker) newMarker.marker.setIcon('/static/marker-orange.svg');
      },
      
      exitViewedProperty({ commit, state }) {
        // Deactivate the previous marker
        const prevMarker = state.map.markers.find(marker => marker.propertyId === state.viewedProperty.id)
        if (prevMarker) prevMarker.marker.setIcon('/static/marker-gray.svg');      

        commit("exitViewedProperty");
      },

      // Load Properties
      fetchProperties({ state, commit }) {

        // If any errors in filters, do nothing
        if (state.filters.find(filter => filter.errors.length)) return;

        commit("searchResultsLoading");

        Properties.get(state.filters)
          .then(response => {
            commit("searchResultsReturned", response.data);
          })
          .catch(error => {
            console.log(error);
          });
      },

      // Load google maps API
      loadGoogleMaps({ state, commit, dispatch }) {
        console.log('loadGoogleMaps')
        loadGoogleMapsAPI({
          key: "AIzaSyACJc_gRMd_G0IsigCgz6klnPptRCE7xE0",
          libraries: ["drawing"]
        })
          .then(googleMaps => {

            const mapInstance = new googleMaps.Map(document.getElementById("map"), {
              zoom: 10,
              center: new googleMaps.LatLng(44.9778, -93.265), //Mpls lat/long
              mapTypeId: "terrain",
              fullscreenControl: false,
              streetViewControl: false
            });

            // Initialize drawing manager
            const drawingManager = new googleMaps.drawing.DrawingManager({
              drawingControl: false
            });
            drawingManager.setMap(mapInstance);

            // Register polygoncomplete eventlistener
            googleMaps.event.addListener(
              drawingManager,
              "polygoncomplete",
              polygonSelection => {
                dispatch('locationPolygonSelected', polygonSelection);

                googleMaps.event.addListener(polygonSelection.getPath(), 'set_at', () => {
                  dispatch('locationPolygonUpdated', polygonSelection);
                });
              }
            );

            commit('registerMap', { googleMaps, mapInstance, drawingManager });

            // Now we can get Properties to put on the map
            // (we could speed this up by fetching it before GMaps is loaded)
            dispatch('fetchProperties');

          })
          .catch(function (err) {
            console.error("Error loading Google Maps API:", err);
          });
      },

      locationDrawRegionClicked({ commit }) {
        commit('locationDrawRegionClicked');
      },

      locationPolygonCleared({ commit }) {
        commit('locationPolygonCleared');
      },

      locationPolygonSelected({ commit }, polygonSelection) {
        console.log('locationPolygonSelected action', polygonSelection);
        commit('locationPolygonSelected', polygonSelection);
      },

      locationPolygonUpdated({ commit }, polygonSelection) {
        commit('locationPolygonUpdated', polygonSelection);
      },

      multiSelectFilterUpdated({ commit }, payload) {
        commit('multiSelectFilterUpdated', payload);
      },

      numericRangeFilterUpdated({ commit }, payload) {
        commit('numericRangeFilterUpdated', payload);
      },

      quarterRangeFilterUpdated({ commit }, payload) {
        commit('quarterRangeFilterUpdated', payload);
      },
    },

    mutations: {

      togglePropertySelectedForExport(state, propertyId) {
        let i = state.propertiesForExport.indexOf(propertyId);
        if (i === -1) {
          state.propertiesForExport.push(propertyId);
        } else {
          state.propertiesForExport.splice(i, 1);
        }
      },

      viewProperty(state, propertyId) {
        state.viewedProperty = state.searchResults.find(property => property.id === propertyId);
      },

      exitViewedProperty(state) {
        state.viewedProperty = null;
      },

      searchResultsLoading(state) {
        state.searchResultsLoading = true;
      },

      searchResultsReturned(state, searchResults) {
        state.searchResultsLoading = false;
        state.searchResults = searchResults;

        // Also update map markers
        state.map.markers = searchResults.map(property => {
          const marker = new state.map.googleMaps.Marker({
            position: {
              lat: parseFloat(property.Lat),
              lng: parseFloat(property.Lng)
            },
            map: state.map.instance,
            title: property["Property Name"],
            icon: '/static/marker-gray.svg'
          });

          // View property detail when marker clicked
          marker.addListener('click', () => {
            this.dispatch('viewPropertyDetailClicked', property.id);  
          });

          return { propertyId: property.id, marker: marker };
        });
      },

      registerMap(state, { googleMaps, mapInstance, drawingManager }) {
        state.map.googleMaps = googleMaps;
        state.map.instance = mapInstance;
        state.map.drawingManager = drawingManager;
      },

      locationDrawRegionClicked(state) {
        state.map.drawingManager.setOptions({
          drawingMode: state.map.googleMaps.drawing.OverlayType.POLYGON,
          polygonOptions: { editable: true }
        });
      },

      locationPolygonSelected(state, polygonSelection) {
        var filter = state.filters.find(filter => filter.id === 'polygon');

        // Use the polygonSelection to populate the filter's selectedValues
        filter.selectedValues = [];
        var vertices = polygonSelection.getPath();
        vertices.forEach((vertex, i) => {
          var coordinates = vertices.getAt(i);
          filter.selectedValues.push({
            lat: coordinates.lat(),
            lng: coordinates.lng()
          });
        });

        // Switch back to regular mode (which allows polygon to be edited)
        state.map.drawingManager.setOptions({
          drawingMode: null
        });

        // Hold onto the polygonSelection instance for clearing later
        state.map.polygonSelection = polygonSelection;

        this.dispatch('fetchProperties');
      },

      locationPolygonUpdated(state, polygonSelection) {
        var filter = state.filters.find(filter => filter.id === 'polygon');

        // Use the polygonSelection to populate the filter's selectedValues
        filter.selectedValues = [];
        var vertices = polygonSelection.getPath();
        vertices.forEach((vertex, i) => {
          var coordinates = vertices.getAt(i);
          filter.selectedValues.push({
            lat: coordinates.lat(),
            lng: coordinates.lng()
          });
        });

        this.dispatch('fetchProperties');
      },

      locationPolygonCleared(state) {
        // Clear the polygon filter's selectedValues
        var filter = state.filters.find(filter => filter.id === 'polygon');
        filter.selectedValues = [];

        // Reset the map's polygonSelection
        state.map.polygonSelection.setMap(null);
        state.map.polygonSelection = null;

        this.dispatch('fetchProperties');
      },

      multiSelectFilterUpdated(state, payload) {
        if (!payload.id || !payload.selectedValues) {
          console.error('Payload expected to contain id and selectedValues, got', payload);
          return;
        }
        var filter = state.filters.find((filter) => {
          return filter.id === payload.id;
        })
        if (!filter) console.error('Filter not found for id:', payload.id);

        // Set selectedValues state, using copies
        filter.selectedValues = payload.selectedValues.slice();

        this.dispatch('fetchProperties');
      },

      numericRangeFilterUpdated(state, payload) {
        if (!payload.id || !payload.selectedValues) {
          console.error('Payload expected to contain id and selectedValues, got', payload);
          return;
        }
        var filter = state.filters.find((filter) => {
          return filter.id === payload.id;
        })

        if (!filter) console.error('Filter not found for id:', payload.id);

        // Set selectedValues state, using copies
        filter.selectedValues = { ...payload.selectedValues };

        // Collect validation errors after new values are set
        // We're ok with the store having invalid values
        // We want the store to represent the most recent user input, even if it's invalid
        filter.errors = NumericRangeFilterValidator.validate(filter);

        this.dispatch('fetchProperties');
      },

      quarterRangeFilterUpdated(state, payload) {
        if (!payload.id || !payload.selectedValues) {
          console.error('Payload expected to contain id and selectedValues, got', payload);
          return;
        }
        var filter = state.filters.find((filter) => {
          return filter.id === payload.id;
        })

        if (!filter) console.error('Filter not found for id:', payload.id);

        // Set selectedValues state, using copies
        filter.selectedValues = { ...payload.selectedValues }

        // Collect validation errors after new values are set
        // We're ok with the store having invalid values
        // We want the store to represent the most recent user input, even if it's invalid
        filter.errors = QuarterRangeFilterValidator.validate(filter);

        this.dispatch('fetchProperties');
      }
    },
    getters: {
      activeLocationFilters: state => {
        return state.filters.filter(filter => filter.subject === 'location');
      },
      activePropertyFilters: state => {
        var propertyFilters = state.filters.filter(filter => filter.subject === 'property');
        return propertyFilters.filter(filter => {
          // Some filters are only active if industrial properties are selected
          if (filter.id === 'percent_office_finish' || filter.id === 'clear_height') {
            var propTypeFilter = state.filters.find(filter => { return filter.id === 'property_types'; });
            return propTypeFilter.selectedValues.indexOf('industrial') !== -1;
          }
          return true;
        })
      },
      activeTransactionFilters: state => {
        return state.filters.filter(filter => filter.subject === 'transaction');
      }
    }
  })
  return store;
}

export default {
  initStore: initStore
};


