<template>
  <div class="property-detail">
    <div class="field" v-for="field in fields">
      <div class="label">{{field.label}}</div>
      <div class="value">{{field.value}}</div>
    </div>
  </div>
</template>

<script>

export default {
  name: "PropertyDetail",
  props: {
    property: Object
  },
  computed: {
    fields() {
      return Object.keys(this.property).map(key =>{
        return {
          label: key,
          value: this.property[key]
        }
      })
    }
  }
};
</script>

<style lang="scss" scoped>
.property-detail {
  padding: 20px 0 40px;
  display: flex;
  flex-wrap: wrap;
}

.field {
  display: flex;
  width: 50%;
  max-width: 500px;
}
.label {
  width: 40%;
  padding: 5px 8px;
  text-align: right;
  font-weight: bold;
}
.value {
  width: 60%;
  padding: 5px 8px;
}
</style>



