<template>
  <div>
    <div>
      <label :id="uniqueId + 'from'">From</label>
      <input :id="uniqueId + 'from'"
             type="number"
             placeholder="Set mininum"
             :value="selectedValues.minYear"
             :min="minYear"
             :max="maxYear"
             @change="minYearChanged($event.target.value)" />
      <select @change="minQuarterChanged($event.target.value)">
        <option value="1"
                :checked="isMinQuarterChecked(1)">Q1</option>
        <option value="2"
                :checked="isMinQuarterChecked(2)">Q2</option>
        <option value="3"
                :checked="isMinQuarterChecked(3)">Q3</option>
        <option value="4"
                :checked="isMinQuarterChecked(4)">Q4</option>
      </select>
    </div>
    <div>
      <label :id="uniqueId + 'to'">To</label>
      <input :id="uniqueId + 'to'"
             type="number"
             placeholder="Set maximum"
             :value="selectedValues.maxYear"
             :min="minYear"
             :max="maxYear"
             @change="maxYearChanged($event.target.value)" />
      <select @change="maxQuarterChanged($event.target.value)">
        <option value="1"
                :checked="isMaxQuarterChecked(1)">Q1</option>
        <option value="2"
                :checked="isMaxQuarterChecked(2)">Q2</option>
        <option value="3"
                :checked="isMaxQuarterChecked(3)">Q3</option>
        <option value="4"
                :checked="isMaxQuarterChecked(4)">Q4</option>
      </select>
    </div>
  </div>
</template>

<script>
import ReasonablyUniqueId from "../services/ReasonablyUniqueId";

export default {
  name: "QuarterRange",
  data() {
    return {
      uniqueId: ReasonablyUniqueId.getReasonablyUniqueId()
    };
  },
  props: {
    minYear: Number,
    minQuarter: Number,
    maxYear: Number,
    maxQuarter: Number,
    selectedValues: Object
  },
  methods: {
    isMinQuarterChecked(value){
      return this.minQuarter === value;
    },
    isMaxQuarterChecked(value){
      return this.maxQuarter === value;
    },

    // Emit events for parent to handle
    minYearChanged(value) {
      this.$emit("minYearChanged", parseInt(value));
    },
    minQuarterChanged(value) {
      this.$emit("minQuarterChanged", parseInt(value));
    },
    maxYearChanged(value) {
      this.$emit("maxYearChanged", parseInt(value));
    },
    maxQuarterChanged(value) {
      this.$emit("maxQuarterChanged", parseInt(value));
    },
  }
};
</script>
<style lang="scss" scoped>

</style>



