<template>
  <div class="property-filters">
    <div v-for="filter in filters"
         :key="filter.id"
         :class="{'property-filter': true, 'error': filter.errors.length > 0 }">
      <label>{{filter.name}}</label>
      <MultiSelect v-if="filter.type === 'multi_select'"
                   :options="filter.options"
                   :selectedValues="filter.selectedValues"
                   @optionClicked="value => multiSelectOptionClicked(filter, value)"
                   @allClicked="() => multiSelectAllClicked(filter)">
      </MultiSelect>
      <NumericRange v-if="filter.type === 'numeric_range'"
                    :min="filter.min"
                    :max="filter.max"
                    :unit="filter.unit"
                    :step="filter.step"
                    :selectedValues="filter.selectedValues"
                    @minChanged="value => numericRangeChanged(filter, 'min', value)"
                    @maxChanged="value => numericRangeChanged(filter, 'max', value)">
      </NumericRange>
      <QuarterRange v-if="filter.type === 'quarter_range'"
                    :minYear="filter.minYear"
                    :minQuarter="filter.minQuarter"
                    :maxYear="filter.maxYear"
                    :maxQuarter="filter.maxQuarter"
                    :selectedValues="filter.selectedValues"
                    @minYearChanged="value => quarterRangeChanged(filter, 'minYear', value)"
                    @minQuarterChanged="value => quarterRangeChanged(filter, 'minQuarter', value)"
    
                    @maxYearChanged="value => quarterRangeChanged(filter, 'maxYear', value)"
                    @maxQuarterChanged="value => quarterRangeChanged(filter, 'maxQuarter', value)">
      </QuarterRange>
      <div class="error-item"
           v-for="error in filter.errors"
           :key="error.$index">{{error}}</div>
    </div>
  </div>
</template>

<script>
import MultiSelect from "@/components/MultiSelect";
import NumericRange from "@/components/NumericRange";
import QuarterRange from "@/components/QuarterRange";

export default {
  name: "Filters",
  components: { MultiSelect, NumericRange, QuarterRange },
  data() {
    return {};
  },
  props: {
    filters: Array
  },
  methods: {
    // Handle the click events for options in MultiSelects
    // Update the central store
    multiSelectOptionClicked(filter, value) {
      var selectedValues = filter.selectedValues.slice();
      var i = selectedValues.indexOf(value);
      if (i === -1) {
        selectedValues.push(value);
      } else {
        selectedValues.splice(i, 1);
      }
      this.$store.dispatch("multiSelectFilterUpdated", {
        id: filter.id,
        selectedValues
      });
    },

    multiSelectAllClicked(filter) {
      // Clear all selections
      var selectedValues = [];
      this.$store.dispatch("multiSelectFilterUpdated", {
        id: filter.id,
        selectedValues
      });
    },
    numericRangeChanged(filter, key, value) {
      // Copy existing and merge in new value
      var selectedValues = { ...filter.selectedValues };
      selectedValues[key] = value;
      this.$store.dispatch("numericRangeFilterUpdated", {
        id: filter.id,
        selectedValues
      });
    },
    quarterRangeChanged(filter, key, value) {
      var selectedValues = { ...filter.selectedValues };
      selectedValues[key] = value;
      this.$store.dispatch("quarterRangeFilterUpdated", {
        id: filter.id,
        selectedValues
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

</style>



