<template>
  <div>
    <p class="no-results" v-if="noResults && !loading">No results found.</p>
    <table :class="{loading: loading}" >
      <thead>
        <tr>
          <th>Export</th>
          <th v-for="column in resultsTableColumns"
              :key="column.id">{{column.displayName}}</th>
          <th></th>
        </tr>
      </thead>
      <tbody v-if="resultsTableRows.length">
        <tr v-for="row in resultsTableRows" 
            :key="row.id">
          <td><input type="checkbox" :checked="isChecked(row.id)" @change="checkboxChanged(row.id)"></td>
          <td v-for="cell in row.cells"
              :key="cell.id">{{cell.content}}</td>
          <td><button class="btn-small" @click="viewDetailClicked(row.id)">View Details</button></td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
const columnsToDisplay = ['Property Name', 'Property Sub-Type', 'Submarket', 'Total NRA SF', 'Total per SF'];

export default {
  name: "SearchResultsTable",
  methods: {
    checkboxChanged(rowId){
      this.$store.dispatch('togglePropertySelectedForExport', rowId); 
    },
    isChecked(rowId){
      return this.$store.state.propertiesForExport.indexOf(rowId) !== -1;
    },
    viewDetailClicked(rowId){
      this.$store.dispatch('viewPropertyDetailClicked', rowId);
    }
  },
  computed: {
    loading() {
      return this.$store.state.searchResultsLoading;
    },
    noResults() {
      return this.searchResults.length === 0;
    },
    searchResults() {
      return this.$store.state.searchResults;
    },
    resultsTableColumns() {
      if (this.searchResults.length === 0) return [];

      // For now just use the first Properties keys for the column headers
      // return Object.keys(this.searchResults[0]).map((key, i) => {
      //   return { displayName: key, id: i };
      // });

      // Use a static list of columns to display
      return columnsToDisplay.map((displayName, i) => {
        return { displayName: displayName, id: i };
      });
    },
    resultsTableRows() {
      if (this.searchResults.length === 0) return [];
      return this.searchResults.map(property => {
        let cellId = 0;
        const row = {};
        row.id = property.id;
        row.cells = columnsToDisplay.map((key) => {
          cellId ++;  
          return { content: property[key], id: (row.id + '-' + cellId) };
        });
        return row;
      });
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>

table {
  width: 100%;
  border-collapse: collapse;
  transition: opacity 0.2s;

  &.loading {
    opacity: 0.5;
  }
}

th,
td {
  padding: 10px 16px;
  border: none;

  button {
    white-space: nowrap;
  }
}

.btn-small {
  padding: 5px 8px;
  font-size: 12px;
}

th {
  vertical-align: bottom;
  text-align: left;
  background: #eee;
}

td {
  vertical-align: middle;
}

tr:nth-child(even) td {
  background: #f3f3f3;
}
</style>



