<template>
  <div class="export">
    <router-link tag="button"
                 to="/search">
      &lt; Back to Seach
    </router-link>
    <h2>Export {{propertiesForExportCount}} Properties</h2>
    <table>
      <thead>
        <tr>
          <th v-for="column in columns"
              :key="column.id">{{column.displayName}}</th>
          <!-- <th></th> -->
        </tr>
      </thead>
      <tbody v-if="rows.length">
        <tr v-for="row in rows"
            :key="row.id">
          <td v-for="cell in row.cells"
              :key="cell.id">{{cell.content}}</td>
          <!-- <td>
            <button class="btn-small"
                    @click="viewDetailClicked(row.id)">View Details</button>
          </td> -->
        </tr>
      </tbody>
    </table>
    <div class="buttons">
      <button>Save all as PDF</button>
      <button>Save all as CSV</button>
    </div>
  </div>
</template>

<script>
const columnsToDisplay = [
  "Property Name",
  "Property Sub-Type",
  "Submarket",
  "Total NRA SF",
  "Total per SF"
];

export default {
  name: "Export",
  components: {},
  computed: {
    propertiesForExportCount() {
      return this.propertiesForExport.length;
    },
    propertiesForExport() {
      return this.$store.state.propertiesForExport.map(propertyId => {
        return this.$store.state.searchResults.find(
          property => property.id === propertyId
        );
      });
    },
    columns() {
      // Use a static list of columns to display
      return columnsToDisplay.map((displayName, i) => {
        return { displayName: displayName, id: i };
      });
    },
    rows() {
      return this.propertiesForExport.map(property => {
        let cellId = 0;
        const row = {};
        row.id = property.id;
        row.cells = columnsToDisplay.map(key => {
          cellId++;
          return { content: property[key], id: row.id + "-" + cellId };
        });
        return row;
      });
    }
  },

  methods: {}
};
</script>

<style lang="scss">
.export {
  padding: 20px;
}

.buttons {
  margin: 30px 0;
  text-align: right;
}

table {
  width: 100%;
  border-collapse: collapse;
  transition: opacity 0.2s;

  &.loading {
    opacity: 0.5;
  }
}

th,
td {
  padding: 10px 16px;
  border: none;

  button {
    white-space: nowrap;
  }
}

.btn-small {
  padding: 5px 8px;
  font-size: 12px;
}

th {
  vertical-align: bottom;
  text-align: left;
  background: #eee;
}

td {
  vertical-align: middle;
}

tr:nth-child(even) td {
  background: #f3f3f3;
}
</style>



