// Simple validator for Quarter Range filters
// Returns an array of errors
export default {
  validate: function (filter) {
    const errors = [];
    var v = filter.selectedValues;

    // First compare years
    // Second, if same year, compare quarters
    if (v.minYear > v.maxYear ||  v.minYear === v.maxYear && v.minQuarter > v.maxQuarter) {
      errors.push('"From" date must be before "To" date.');
    };

    if (typeof v.minYear !== 'number' || isNaN(v.minYear)) {
      errors.push('"From" year is required.');
    }

    if (typeof v.maxYear !== 'number' || isNaN(v.maxYear)) {
      errors.push('"To" year is required.');
    }

    if (v.minYear < filter.minYear) {
      errors.push('"From" year cannot be before ' + filter.minYear);
    }

    if (v.maxYear > filter.maxYear) {
      errors.push('"To" year cannot be after ' + filter.maxYear);
    }
    return errors;
  }
};


