import Vue from 'vue';
import Router from 'vue-router';
import Login from '@/components/Login';
import Search from '@/components/Search';
import Export from '@/components/Export';
import Upload from '@/components/Upload';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login,
    }, {
      path: '/search',
      name: 'Search',
      component: Search
    },
    {
      path: '/export',
      name: 'Export',
      component: Export
    },
    {
      path: '/upload',
      name: 'Upload',
      component: Upload
    }
  ],
});



