// Simple validator for Numeric Range filters
// Returns an array of errors
export default {
  validate: function (filter) {
    const errors = [];

    if (filter.selectedValues.min >= filter.selectedValues.max) {
      errors.push('Min must be smaller than max.');
    };

    if (typeof filter.selectedValues.min !== 'number' || isNaN(filter.selectedValues.min)) {
      errors.push('Min is required.');
    }

    if (typeof filter.selectedValues.max !== 'number' || isNaN(filter.selectedValues.max)) {
      errors.push('Max is required.');
    }

    if (filter.selectedValues.min < filter.min) {
      errors.push('Min cannot be less than ' + filter.min);
    }

    if (filter.selectedValues.max > filter.max) {
      errors.push('Max cannot be more than ' + filter.max);
    }
    return errors;
  }
};


