<template>
  <div class="upload">
    <h2>Contribute</h2>
    <p class="featured">Lease Vault is the industry's highest quality source for competitive lease data.
      Our data is contributed by licensed appraisers, like you, and reviewed by a
      team of lightly trained monkeys.</p>
    <p>Current credits: <strong>{{credits}}</strong> </p>
    <h3>What kind of information can I contribute?</h3>
    <ul>
      <li>Lease Documents</li>
      <li>Finished Appraisals</li>
      <li>Rent Rolls and other support documents</li>
    </ul>
    <p>Credits receive are determined by completeness, quality and recency of data submitted.</p>

    <h3>There are three ways to contribute:</h3>

    <p class="chooser">
      <button @click="selectedSection = 'manual'"
              :class="{ active: selectedSection === 'manual'}">Manual Entry</button>
      <button @click="selectedSection = 'csv'"
              :class="{ active: selectedSection === 'csv'}">CSV + Attachments</button>
      <button @click="selectedSection = 'concierge'"
              :class="{ active: selectedSection === 'concierge'}">Concierge Service</button>
    </p>

    <div v-if="selectedSection === 'manual'">
      [[Manual Web Form]]
    </div>
    <div v-if="selectedSection === 'csv'">
      <p>Select files to upload [lotsa filetype logos]</p>
      <button @click="openFilePicker()">🔒 Secure Upload</button>
      <p v-for="upload in response.filesUploaded"
         :key="upload.uploadId">
        ✅ Attachment saved:
        <a :href="upload.url">{{upload.filename}}</a> ({{upload.size/8}} bytes)
      </p>
      <div v-if="response.filesUploaded">      
        <p><input type="checkbox" id="certify"> <label for="certify">I, Algernon Picklestick, certify that the attached files are real and accurate</label></p>
        <p><label>License Number</label><input type="text" placeholder="My appraiser license"></p>
        <p><button>Submit</button></p>
      </div>
    </div>
    <div v-if="selectedSection === 'concierge'">
      <p class="featured">Transforming real-world appraisal documents into useable Data can be hard,
        and tedious!</p>
      <p> Don't worry, we've got you covered. We do this for a living!</p>
      <p>
        <a href="mailto:concierge@lease-vault.com">Contact us</a> to find out about how we can help.</p>
    </div>

  </div>
</template>

<script>
import filestack from "filestack-js";

// TODO: DON'T GO TO PROD WITH THIS!  Implement HMAC and other security features
const apikey = "ASO7piUpHQD6vF6erh66rz";
const fsClient = filestack.init(apikey);

export default {
  name: "Upload",
  data() {
    return {
      response: "",
      selectedSection: ""
    };
  },
  computed: {
    credits() {
      return this.$store.state.credits;
    }
  },
  methods: {
    openFilePicker() {
      fsClient
        .pick({
          fromSources: [
            "local_file_system",
            "googledrive",
            "dropbox",
            "box",
            "onedrive"
          ],
          accept: [
            "image/*",
            ".pdf",
            ".doc",
            ".docx",
            ".docm",
            ".csv",
            ".xls",
            ".xslx",
            ".zip"
          ],
          maxSize: 1024000000,
          maxFiles: 20,
          minFiles: 1
        })
        .then(response => {
          this.response = response;
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.upload {
  max-width: 700px;
  margin: 0 auto;
  padding: 40px;
}

.featured {
  font-size: 15px;
  font-weight: lighter;
}

.chooser button {
  margin-right: 10px;
}

button {
  background: #007ee5;

  &.active {
    transform: translateY(-2px);
  }
}
</style>



