<template>
  <div>
    <div>
      <label :id="uniqueId + 'from'">From</label>
      <span>{{unitPrefix}}</span>
      <input :id="uniqueId + 'from'"
             type="number"
             placeholder="Set mininum"
             :value="selectedValues.min"
             :step="step"
             :min="min"
             :max="max"
             @change="minChanged($event.target.value)" />
      <span>{{unitPostfix}}</span>
    </div>
    <div>
      <label :id="uniqueId + 'to'">To</label>
      <span>{{unitPrefix}}</span>
      <input :id="uniqueId + 'to'"
             type="number"
             placeholder="Set maximum"
             :value="selectedValues.max"
             :step="step"
             :min="min"
             :max="max"
             @change="maxChanged($event.target.value)" />
      <span>{{unitPostfix}}</span>
    </div>
  </div>
</template>

<script>
import ReasonablyUniqueId from "../services/ReasonablyUniqueId";

export default {
  name: "NumericRange",
  data() {
    return {
      uniqueId: ReasonablyUniqueId.getReasonablyUniqueId()
    };
  },
  props: {
    step: Number,
    min: Number,
    max: Number,
    step: Number,
    unit: String,
    selectedValues: Object
  },
  computed: {
    // Only use prefix for '%', every other unit is placed after the input
    unitPrefix(){
      return this.unit === '%' ? '%' : null;
    }, 
    unitPostfix(){
      return this.unit !== '%' ? this.unit : null;
    }
  },
  methods: {
    // Emit events for parent to handle
    minChanged(value) {
      this.$emit("minChanged", parseInt(value));
    },
    maxChanged(value) {
      this.$emit("maxChanged", parseInt(value));
    }
  }
};
</script>
<style lang="scss" scoped>

</style>



