<template>
  <div class="header">
    <div class="logo">Lease Vault</div>
    <div class="header-nav"
         v-if="loggedIn">
      <div class="header-nav-item">
        <router-link class="header-nav-item"
                     to="/search">
          Search
        </router-link>
      </div>
      <div class="header-nav-item">
        <router-link class="header-nav-item"
                     to="/upload">
          Contribute
        </router-link>
      </div>
      <div class="header-nav-item">
        Credits: {{credits}}
      </div>
      <router-link class="header-nav-item"
                   to="/">
        Logout
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "AppHeader",
  computed: {
    loggedIn() {
      // For now, just make sure we aren't on the login screen
      // TODO: wire to auth
      return this.$route.path !== "/";
    },
    credits() {
      return this.$store.state.credits;
    }
  }
};
</script>

<style lang="scss" scoped>
.header {
  height: 30px;
  padding: 5px 8px;
  background: #000;
  color: #fff;
  flex-grow: 0;
  display: flex;
  justify-content: space-between;

  .logo {
    line-height: 20px;
    font-size: 16px;
    font-weight: bold;
  }
}

.header-nav {
  display: flex;
}
.header-nav-item {
  padding: 0 10px;
}
</style>



