<template>
  <div id="app">
    <AppHeader></AppHeader>
    <router-view/>
  </div>
</template>

<script>
import AppHeader from "@/components/AppHeader";

export default {
  name: "app",
  components: { AppHeader }
};
</script>

<style lang="scss">
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;

  * {
    box-sizing: inherit;
  }
}

#app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

a {
  color: inherit;
  
  &:visited {
    color: inherit;
  }
}

button {
  appearance: none;
  background: #f90;
  border: none;
  border-radius: 3px;
  font-size: inherit;
  padding: 8px 16px;
  color: white;
  font-weight: bold;
  cursor: pointer;
  transition: transform 0.1s, box-shadow 0.1s;

  &:hover {
    box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.2);
    transform: translateY(-2px);
  }
}
</style>



