export default {
  getReasonablyUniqueId: function(){
    // Reasonably unique ID 
    // Unique enough for matching <label>s and <input>s 
    // Should not be used for any data persistence
    // Random number between 1-1000 + the last 8 digits of epoch time (milliseconds)
    return ('' + Math.random()*999|0) + ('' + new Date().getTime()).slice(5,13) + '_'
  }
};


