<template>
  <div class="search-screen">
    <div class="filters">
      <div class="filters-1">
        <h3>Transactions</h3>
        <Filters :filters="transactionFilters"></Filters>
      </div>
      <div class="filters-2">
        <h3>Location</h3>
        <PolygonFilter></PolygonFilter>
        <h3>Property</h3>
        <Filters :filters="propertyFilters"></Filters>
      </div>
    </div>
    <div class="map-and-results">
      <div id="map"></div>
      <div class="results">
        <div class="results-header"
             v-if="!viewedProperty">
          <div class="left">
            <strong>{{resultsCount}}</strong> Properties Found</div>
          <div class="right">
            <div v-if="propertiesForExportCount === 0">
              Select properties for export
            </div>
            <div v-if="propertiesForExportCount > 0">
              <strong>{{propertiesForExportCount}}</strong> Selected For Export
              <router-link tag="button"
                           to="/export">
                Export > 
              </router-link>
            </div>
          </div>
        </div>
        <div class="results-header"
             v-if="viewedProperty">
          <div class="left">
            <button class="view-all-button"
                    @click="viewAllClicked">&lt; Back to Results</button>
            <strong>{{viewedProperty['Property Name']}}</strong>
          </div>
          <div class="right">
            <input :id="'selectForExport' + viewedProperty.id"
                   type="checkbox"
                   :checked="isSelectedForExport(viewedProperty.id)"
                   @change="selectForExportChanged(viewedProperty.id)">
            <label :for="'selectForExport' + viewedProperty.id">Export</label>
          </div>
        </div>
        <SearchResultsTable v-if="!viewedProperty"></SearchResultsTable>
        <PropertyDetail v-if="viewedProperty"
                        :property="viewedProperty"></PropertyDetail>
      </div>
    </div>
  </div>
</template>

<script>
import Filters from "@/components/Filters";
import PolygonFilter from "@/components/PolygonFilter";
import SearchResultsTable from "@/components/SearchResultsTable";
import PropertyDetail from "@/components/PropertyDetail";

export default {
  name: "Search",
  components: { Filters, PolygonFilter, SearchResultsTable, PropertyDetail },
  computed: {
    propertiesForExportCount() {
      return this.$store.state.propertiesForExport.length;
    },
    viewedProperty() {
      return this.$store.state.viewedProperty;
    },
    locationFilters() {
      return this.$store.getters.activeLocationFilters;
    },
    propertyFilters() {
      return this.$store.getters.activePropertyFilters;
    },
    transactionFilters() {
      return this.$store.getters.activeTransactionFilters;
    },
    resultsCount() {
      return this.$store.state.searchResults.length;
    }
  },

  methods: {
    viewAllClicked() {
      this.$store.dispatch("exitViewedProperty");
    },
    isSelectedForExport(propertyId) {
      return this.$store.state.propertiesForExport.indexOf(propertyId) !== -1;
    },
    selectForExportChanged(propertyId) {
      this.$store.dispatch("togglePropertySelectedForExport", propertyId);
    }
  },

  // Once mounted (DOM is ready), load the Google Maps API
  mounted() {
    this.$store.dispatch("loadGoogleMaps");
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
.search-screen {
  flex-grow: 1;
  display: flex;
  flex-direction: rows;
  overflow: hidden;
}

.filters {
  position: relative;
  flex-grow: 0;
  display: flex;
  z-index: 2;

  .filters-1,
  .filters-2 {
    height: 100%;
    overflow-y: auto;
    overflow-x: hidden;
    width: 250px;
  }
  .filters-2 {
    box-shadow: 1px 0px 10px 0px rgba(0, 0, 0, 0.4);
  }

  h3 {
    margin: 0;
    padding: 5px 8px;
    text-transform: uppercase;
    letter-spacing: 1px;
    font-size: 11px;
    background: #f0f0f0;
  }

  .property-filter {
    padding: 5px 8px;
    border-bottom: 1px solid #eee;

    &:last-child {
      border-bottom: none;
    }

    &.error {
      background: rgba(255, 0, 0, 0.1);
    }
  }

  .error-item {
    font-weight: bold;
    color: red;
  }
}

.map-and-results {
  flex-grow: 1;
  max-width: 100%;
  overflow: auto;
}

#map {
  min-height: 40%;
}

.results {
  position: relative;
  width: 100%;
  flex-grow: 1;
  overflow-x: auto;
  box-shadow: 0 -10px 10px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.results-header {
  display: flex;
  justify-content: space-between;
  padding: 10px 16px 10px 24px;
  border-bottom: 1px solid #f0f0f0;
  line-height: 50px;

  strong {
    font-size: 18px;
  }
}

.view-all-button {
  margin-right: 10px;
}
</style>



