<template>
  <div class="property-filter">
    <label>Polygon</label>
    <div v-if="!polygonPoints.length">
      <button @click="drawRegionClicked">Draw Region</button>
    </div>
    <div v-if="polygonPoints.length">
      <ol>
        <li v-for="point in polygonPoints"
            :key="point.id">
          {{point.lat}}, {{point.lng}}
        </li>
      </ol>
      <button @click="polygonCleared">Clear</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "PolygonFilter",
  data() {
    return {};
  },
  computed: {
    polygonPoints() {
      var polygonFilter = this.$store.state.filters.find(
        filter => filter.id === "polygon"
      );
      if (!polygonFilter) return [];
      return polygonFilter.selectedValues;
    }
  },
  methods: {
    drawRegionClicked() {
      this.$store.dispatch("locationDrawRegionClicked");
    },
    polygonCleared() {
      this.$store.dispatch("locationPolygonCleared");
    },
    polygonSelected(polygonSelection) {
      this.$store.dispatch("locationPolygonSelected", polygonSelection);
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.property-filter {
  padding: 10px 0;
  border-bottom: 1px solid #eee;

  &:last-child {
    border-bottom: none;
  }

  &.error {
    background: rgba(255, 0, 0, 0.1);
  }
}

.error-item {
  font-weight: bold;
  color: red;
}
</style>



