<template>
  <div>
    <div>
      <input :id="uniqueId + 'all'" type="checkbox" value="all" :checked="isAllChecked" @click="allClicked()">
      <label :for="uniqueId + 'all'">All</label>
    </div>
    <div v-for="option in options" :key="option.value">
      <input :id="uniqueId + option.value" type="checkbox" :value="option.value" :checked="isChecked(option)" @click="optionClicked($event.target.value)">
      <label :for="uniqueId + option.value">{{option.name}}</label>
    </div>
  </div>
</template>

<script>
import ReasonablyUniqueId from '../services/ReasonablyUniqueId'

export default {
  name: "MultiSelect",
  data(){
    return {
      uniqueId: ReasonablyUniqueId.getReasonablyUniqueId()
    }
  }, 
  props: {
    options: Array,
    selectedValues: Array
  },
  computed: {
    isAllChecked(){
      return this.selectedValues.length === 0;
    }
  },
  methods: {
    isChecked(option){
      return this.selectedValues.indexOf(option.value) !== -1
    },
    
    // Trigger event for parent component to manage
    optionClicked(value) {
      this.$emit('optionClicked', value);
    },

    allClicked(){
      this.$emit('allClicked');
    }
  }
};
</script>
<style lang="scss" scoped>

</style>



